import {
    Component,
    EventEmitter,
    Input,
    DoCheck,
    OnChanges,
    OnDestroy,
    OnInit,
    Output,
    SimpleChanges,
    AfterViewInit,
    AfterContentInit,
    AfterViewChecked
} from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Subscription } from 'rxjs';

import { Thin2Logger } from 'thin2-log';

import { Th2GenericFormConfigModel } from './models/th2-generic-form-config.model';
import { Th2GenericFormViewModel } from './models/th2-generic-form-view.model';
import { Th2GenericFormChangeModel } from './models/th2-generic-form-change.model';

export interface Th2GenericFormCreated {
    id: string;
    formGroup: FormGroup;
    formConfig: Th2GenericFormConfigModel;
}

export interface Th2GenericFormConfigModelChanged {
    formGroup: FormGroup;
    formConfig: Th2GenericFormConfigModel;
}

@Component({
    selector: 'th2-generic-form-component',
    templateUrl: './th2-generic-form.component.html'
})
export class Th2GenericFormComponent implements AfterViewInit, OnChanges, OnDestroy, OnInit {
    @Input()
    public formConfig: Th2GenericFormConfigModel;
    @Output()
    public formSubmit: EventEmitter<any> = new EventEmitter<any>();
    @Output()
    public formClear: EventEmitter<boolean> = new EventEmitter<boolean>();
    @Output()
    public formChange: EventEmitter<Th2GenericFormChangeModel> = new EventEmitter<
        Th2GenericFormChangeModel
    >();
    @Output()
    public formCreated: EventEmitter<Th2GenericFormCreated> = new EventEmitter<
        Th2GenericFormCreated
    >();
    @Output()
    public formConfigChanged: EventEmitter<Th2GenericFormConfigModelChanged> = new EventEmitter<
        Th2GenericFormConfigModelChanged
    >();

    public formGroup: FormGroup;

    private subscriptions: Subscription[] = [];

    constructor(private formBuilder: FormBuilder, private LOGGER: Thin2Logger) {}

    public ngAfterViewInit() {
        const formCreatedinfo: Th2GenericFormCreated = {
            id: this.formConfig.id,
            formGroup: this.formGroup,
            formConfig: this.formConfig
        };
        this.formCreated.emit(formCreatedinfo);
    }

    public ngOnDestroy() {
        // Remove subscriptions on component destroy
        this.subscriptionsRemoved();
    }

    public ngOnInit() {
        this.formGroup = this.initializeFormControl(this.formConfig);
        this.subscriptions = this.initializeFormSubscriptions(this.formConfig);
    }

    // Change input config treatment
    public ngOnChanges(changes: SimpleChanges): void {
        // Remove last config subscriptions
        this.subscriptionsRemoved();

        // Restart controls and subscriptions
        this.formGroup = this.initializeFormControl(this.formConfig);
        this.subscriptions = this.initializeFormSubscriptions(this.formConfig);

        this.formConfigChanged.emit({
            formConfig: this.formConfig,
            formGroup: this.formGroup
        });
    }

    // ok 按照配置信息定义div的样式
    public processClass(viewConfig: Th2GenericFormViewModel): string {
        // FIXME: Do we want padding here?
        // Validate that the base component specifies a view :)
        if (viewConfig.view) {
            const breakpoint = viewConfig.view.breakpoint; // sm

            const cols = viewConfig.view.cols; // 12
            const offset = viewConfig.view.offset; // 0

            const sizeClass = breakpoint ? `col-${breakpoint}-${cols}` : `col-${cols}`;

            if (offset) {
                const offsetClass = breakpoint
                    ? `offset-${breakpoint}-${offset}`
                    : `offset-${offset}`;
                return `${offsetClass} ${sizeClass}`;
            } else {
                return sizeClass;
            }
        } else {
            return '';
        }
    }

    // ok 按照配置信息定义选项部分的样式
    public evaluateDirection(): string {
        if (this.formConfig.view && this.formConfig.view.direction) {
            return `d-flex flex-wrap flex-${this.formConfig.view.direction}`;
        } else {
            return 'd-flex flex-wrap flex-row';
        }
    }

    // ok 定义按钮样式
    public processButtonClass() {
        if (this.formConfig.labels && this.formConfig.labels.justify) {
            const breakpoint = this.formConfig.labels.breakpoint;
            const justify = this.formConfig.labels.justify;
            const baseClass = 'row m-0 px-4 pt-3 align-items-center justify-content';

            const sizedClass = breakpoint
                ? `${baseClass}-${breakpoint}-${justify}`
                : `${baseClass}-${justify}`;

            return sizedClass;
        } else {
            return 'row m-0 px-4 pt-3 justify-content-start align-items-center';
        }
    }

    // ok 重置表格
    public clear() {
        // Clear form data
        this.formGroup.reset();

        this.LOGGER.debug('Form cleared');
        this.formClear.emit(true);
    }

    // ok 提交，发送表格数据
    public submit() {
        // Extract form data
        const formValue = this.formGroup.value; // filterForm

        this.LOGGER.debug(`Submitted ${JSON.stringify(formValue)}`);
        this.formSubmit.emit(formValue);
    }

    public forceChange() {
        // Remove last config subscriptions
        this.subscriptionsRemoved();

        // Restart controls and subscriptions
        this.formGroup = this.initializeFormControl(this.formConfig);
        this.subscriptions = this.initializeFormSubscriptions(this.formConfig);
    }

    private initializeFormControl(formConfig: any): FormGroup {
        const configElement = {};
        formConfig.elements.forEach((element) => {
            if (element.title) {
                configElement[element.name] = this.initializeFormControl(element);
            } else {
                // If the element is hidden, do not add validators
                configElement[element.name] = [
                    { value: element.default, disabled: element.disabled },
                    element.displayed ? element.validators : []
                ];
            }
        });

        return this.formBuilder.group(configElement);
    }

    private initializeFormSubscriptions(formConfig: any, path: string[] = []): Subscription[] {
        let subscriptions: Subscription[] = [];
        formConfig.elements.forEach((element) => {
            if (element.title) {
                path.push(element.name);
                subscriptions = subscriptions.concat(
                    this.initializeFormSubscriptions(element, path)
                );
                path.pop();
            } else {
                const subscriptionEmitter = this.subscriptionEmitterFactory(element.name);
                const formControlPath = `${path.join('.')}.${element.name}`;
                const subscription = this.formGroup
                    .get(formControlPath)
                    .valueChanges.subscribe(subscriptionEmitter);
                subscriptions.push(subscription);
            }
        });
        return subscriptions;
    }

    private subscriptionEmitterFactory(control: string) {
        return (value) => {
            this.formChange.emit({ control, value });
        };
    }

    private subscriptionsRemoved() {
        this.subscriptions.forEach((subscription) => {
            subscription.unsubscribe();
        });
    }
}
