import { Component, Input, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';

import { Thin2Logger } from 'thin2-log';

import { Th2GenericFormGroupModel } from './models/th2-generic-form-group.model';
import { Th2GenericFormAttributesModel } from './models/th2-generic-form-attributes.model';

@Component({
    selector: 'th2-generic-form-group-component',
    templateUrl: './th2-generic-form-group.component.html'
})
export class Th2GenericFormGroupComponent implements OnInit {
    @Input()
    public formConfig: Th2GenericFormGroupModel;
    @Input()
    public formGroup: FormGroup;

    constructor(private LOGGER: Thin2Logger) {}

    public ngOnInit() {
        this.LOGGER.debug(`Rendering FormGroup ${this.formConfig.title}`);
    }

    public processClass(
        viewConfig: Th2GenericFormGroupModel | Th2GenericFormAttributesModel
    ): string {
        let finalClass = '';

        // In Th2GenericFormAttributesModel don't existe subsection property! Avoiding error
        if (viewConfig['sectionType'] && viewConfig['sectionType'] !== 'collapsible') {
            finalClass += `p-0 mt-3 `;
        }

        // FIXME: Do we want padding here? SOLVED (improvable)

        // Validate that the base component specifies a view :)
        if (viewConfig.view) {
            const breakpoint = viewConfig.view.breakpoint;
            const cols = viewConfig.view.cols;
            const offset = viewConfig.view.offset;
            const margin = viewConfig.view.margin;
            const padding = viewConfig.view.padding;
            const align = viewConfig.view.align;

            finalClass += breakpoint ? `col-${breakpoint}-${cols}` : `col-${cols}`;

            if (offset) {
                const offsetClass = breakpoint
                    ? `offset-${breakpoint}-${offset}`
                    : `offset-${offset}`;
                finalClass = `${offsetClass} ${finalClass}`;
            }

            if (margin) {
                finalClass += ` ${margin}`;
            }

            if (padding) {
                finalClass += ` ${padding}`;
            }

            if (align) {
                finalClass += ` d-flex align-items-${align}`;
            }
        } else {
            finalClass += 'col';
        }

        return finalClass;
    }

    public evaluateDirection(): string {
        if (this.formConfig.view && this.formConfig.view.direction) {
            return `d-flex flex-wrap flex-${this.formConfig.view.direction}`;
        } else {
            return 'd-flex flex-wrap flex-row';
        }
    }
}
